@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Mulish', sans-serif;
}
body{
    /* background-color: #333; */
    color: #fff;
    
}
.bgContainer{
  position: absolute;
  z-index: -1;
  width: 100%;
  min-height: 100vh;
  background-image: url(/reactweather/static/media/pic.07d08ceb.jpeg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center center;
    opacity: 0.85;
}
.App {
  text-align: center;
}
.weathercont{
  max-width: 600px;
  margin: 0px auto 0px;
  width: 100%;
  padding: 30px 0px 0px;
}
input{
    max-width: 285px;
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 3px;
    outline: 0;
    font-size: 15px;
    font-weight: 500;
}
button{
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  padding: 10px 25px;
  letter-spacing: 1px;
  border: none;
  background: crimson;
  color: #fff;
  margin: 15px 0px 0px 20px;
  border-radius: 3px;
  outline: 0;
  text-transform: uppercase;
}
.weatherWrap{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  margin: 20px 0px 0px;
  width: 100%;
}
.cityWrap{
  padding: 30px 0px 15px;
}
.cityName{
  font-size: 56px;
  font-weight: 600;
}
.temp{
  font-size: 60px;
  font-weight: 800;
  box-shadow: inset 0px 10px 30px 0px rgba(0,0,0,.3), inset 0px 10px 30px 0px rgba(0,0,0,.3);
  max-width: 190px;
  margin: 13px auto -13px;
  width: 100%;
  line-height: 170%;
  border-radius: 3px;
}
.desc{
  font-size: 21px;
  text-transform: capitalize;
  font-weight: 300;
  line-height: 135%;

}
img{
  width: 150px;
    display: inline-block;
}
.alert-danger {
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}
.alert {
  position: relative;
  padding: .75rem 1.25rem;
  /* margin-bottom: 1rem; */
  border: 1px solid transparent;
  border-radius: .25rem;
  margin: 20px auto;
  width: 100%;
}
@media only screen and (max-width: 440px){
  .cityName {
    font-size: 40px;
}
  .desc {
    font-size: 16px;
  }
  .temp {
    font-size: 57px;
  }
  img {
    width: 125px;
  }
  input, .alert{
    width: 92%;
  }
}
